function csi_grab_team_info() {

	$('.team-list .team-member').on('click', function(e){
		e.preventDefault();
		var teamInfo 	= $(this).find('.team-member-title').clone(),
			teamBio 	= $(this).find('.team-member-bio > *').clone(),
			teamPerson 	= teamInfo.add(teamBio);

		$('.team-description').addClass('text-blue').html(teamPerson);

		$(document.body).trigger("sticky_kit:recalc");

	});

}

csi_grab_team_info();


function csi_team_sticky_kit() {
	var headerHeight = $('.site-banner').outerHeight();

	$('.our-team-text-wrap').stick_in_parent({
		offset_top: headerHeight + 20,
		spacer: true
	});

}

csi_team_sticky_kit();